.widget {
    width: 100%;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 2px;
    position: relative;
}

.widgetHeader {
    height: 35px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    font-size: 13px;
}

.widgetBody {
    padding: 12px 20px 15px;
}

.title {
    font-size: 13px;
    line-height: 20px;
    text-decoration: none;
    color: #000;
}

.count {
    color: #ccc;
    margin: 0 5px;
}

.text {
    line-height: 28px;
    color: #000;
    text-align: center;
    font-size: 19px;
    padding-bottom: 28px;
    word-wrap: break-word;
}

.description {
    font-size: 13px;
    color: #656565;
    line-height: 20px;
    margin: -22px 0 33px;
    text-align: center;
    word-wrap: break-word;
}

.widgetFooter {
    display: block;
    padding: 13px 0 14px;
    cursor: pointer;
    color: #2a5885;
    background-color: #fff;
    border-top: 1px solid #e7e8ec;
    border-radius: 0 0 4px 4px;
    text-align: center;
    font-size: 12px;
    text-decoration: none;
    -o-transition: background-color 40ms linear;
    transition: background-color 40ms linear;
}